





















































































	import mpvuePicker from '../../components/mpvuePicker.vue'
	import mInput from '../../components/m-input.vue'
		var  graceChecker = require("../../common/graceChecker.js");
export default {
	components: {
		mpvuePicker,
		 mInput
	},
		data() {
			return {
				name:"李白",
				payname:"微信",
				payType:1,
				payaccount:"LIBAI001WEIXIN",
				paypassword:'',
				code:'',
				tel:'',
				
				btntxt: '获取验证码',
				
				disabled:false,
				menuBorder: true,
				menuArrow: false,
				menuCard: true,
				StatusBar: this.StatusBar,
				CustomBar: this.CustomBar,
				themeColor: '#007AFF',
				mode: '',
				deepLength: 1,
				pickerValueDefault: [0],
				pickerValueArray:[],
				pickerSingleArray: [{
						label: '微信',
						value: 0
					},
					{
						label: '支付宝',
						value: 1
					},
					{
						label: '银行卡',
						value: 2
					}
				],
				list: [],
				token:'',
				last_tel:'',
			};
		},
		onLoad() {
			const value = uni.getStorageSync('agentInfo');
			if (value) {
				this.token=value.token;
				this.tel=value.agent_tel
			}
			const last_tel = uni.getStorageSync('last_tel');
			if (last_tel) {
				this.last_tel=last_tel;
				console.log(last_tel);
			}
			uni.request({
				url: 'http://192.168.0.199:8080/agent/agent/last-sms-time',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				method: 'POST',
				dataType: 'json',
				cache: false,
				data: {
					token:this.token,
					last_tel: last_tel,
					sms_type: 2,
				},
				success: res => {
					let lists = res;
					let data = lists.data
					if (data.code == 200) {
						this.time = 60 - parseInt(data.data);
						this.disabled = true;
						this.timer();
					} else if(data.code==400) {
						this.time = 0;
						this.disabled = false;
					} else if(data.code==-200){
						uni.showModal({
								showCancel:false,
								content: '用户信息已失效，请重新登陆',
								success: function (res) {
									if (res.confirm) {
											uni.redirectTo({
												url: '../login/login'
											});
									}
								}
							});
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
			});
			uni.request({
				url: 'http://192.168.0.199:8080/agent/agent/ajax-get-info',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					dataType: 'json',
					cache: false,
					data: {
						token:this.token,
					},
					success: res => {
						if(res.data.code==200){
							let data=res.data.data;
							if(data){
								this.name=data.current_payname;
								if(data.current_paytype=='0'){
									this.payname='微信';
									this.payaccount=data.wechat_num;
								}else if(data.current_paytype=='1'){
									this.payname='支付宝';
									this.payaccount=data.alipay_num;
								}else if(data.current_paytype=='2'){
									this.payname='银行卡';
									this.payaccount=data.bank_num;
								}else if(data.current_paytype==''){
									this.isaccount=false;
								}
							}
						}else if(res.data.code==-200){
							uni.showModal({
								showCancel:false,
								content: '用户信息已失效，请重新登陆',
								success: function (res) {
									if (res.confirm) {
											uni.redirectTo({
												url: '../login/login'
											});
									}
								}
							});
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			
			
		},
		methods: {
			navTo(){
				uni.navigateTo({
					url: 'deposit'
				});
			},
			// 单列
			PickerChange() {
				this.pickerValueArray = this.pickerSingleArray
				this.mode = 'selector'
				this.deepLength = 1
				this.pickerValueDefault = [0]
				this.$refs.mpvuePicker.show()
			},
			onBackPress() {
				if (this.$refs.mpvuePicker.showPicker) {
					this.$refs.mpvuePicker.pickerCancel();
				  return true;
				}
			},
			onUnload() {
				if (this.$refs.mpvuePicker.showPicker) {
					this.$refs.mpvuePicker.pickerCancel()
				}
			},
			//选中省男女
			onConfirm(e) {
				//JSON.stringify(e)
				this.payname = e.label;
				this.payType=e.value[0];
				this.payaccount="";
				console.log(this.payType);
			},
			formSubmit: function (e) {
				//将下列代码加入到对应的检查位置
				//定义表单规则
				var rule = [
					{name:"name", checkType : "string", checkRule:"0,20",  errorMsg:"姓名应为0-20个字符之内"},
					{name:"payname", checkType : "in", checkRule:"微信,支付宝，银行卡",  errorMsg:"请选择账号类型"},
					{name:"payaccount", checkType : "string", checkRule:"0,30",  errorMsg:"请正确填写账号"},
					{name:"paypassword", checkType : "string", checkRule:"4,30", errorMsg:"密码最短为4个字符"},
				];
				//进行表单检查
				var formData = e.detail.value;
				var checkRes = graceChecker.check(formData, rule);
				if(checkRes){

					uni.request({
						url: 'http://192.168.0.199:8080/agent/agent/ajax-edit-deposit',
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							method: 'POST',
							dataType: 'json',
							cache: false,
							data: {
								token:this.token,
								name:formData['name'],
								payType:formData['payType'],
								payaccount:formData['payaccount'],
								paypassword:formData['paypassword'],
								code:this.code,
								last_tel:this.last_tel,
								sms_type:2,
							},
							success: res => {
								this.list = res;
								let data = this.list.data;
								let msg = data.data;
								if(data.code == 200){
									uni.showToast({title:"修改成功!", icon:"none"});
									setTimeout(function() {
										uni.redirectTo({
											url: '../about/deposit'
										});
									}, 2000);
								}
							},
							fail: () => {},
							complete: () => {}
						});
					}
			},
			//获取验证码
			sendcode() {
				if (this.tel == '') {
					uni.showToast({
						icon: 'none',
						title: '手机号码为空'
					}, 2000);
					return;
				} 
				uni.request({
					url: 'http://192.168.0.199:8080/agent/agent/ajax-send-sms',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
						},
					method: 'POST',
					dataType: 'json',
					cache: false,
					data: {
						token:this.token,
						tel: this.tel,
						sms_type: 2,
					},
					success: res => {
						this.list = res;
						let data = this.list.data;
						let msg = data.data;
						if (data.code == 200) {
							this.time = 60;
							this.disabled = true;
							uni.showToast({
								icon: 'none',
								title: msg,
							}, 2000);
							let self = this;
							//记录,成功发送验证码手机号
							uni.setStorageSync('last_tel', this.tel);
							this.timer();
						}
						if (data.code == 400) {
							uni.showToast({
								icon: 'none',
								title: "请稍候再试",
							}, 2000);
							this.time = 0;
							setTimeout(this.timer, 1000);
							this.disabled = false;
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					},
					complete: () => {}
				});
			},
			//倒计时
			timer() {
				if (this.time > 0) {
					this.time--;
					this.btntxt = this.time + 's后获取';
					setTimeout(this.timer, 1000);
				} else {
					this.time = 0;
					this.btntxt = '获取验证码';
					this.disabled = false;
				}
			},
			formReset: function (e) {
				console.log("清空数据")
				this.chosen = ''
			}
		}
	}
